.list {
  background: ghostwhite;
  box-shadow: 0 0 1px;
  position: absolute;
  top: 100px;
  left: 180px;
  width: 200px;
  display: flex;
  justify-content: end;
  cursor: pointer;
}

.list > ul > li:hover {
  text-decoration: underline;
}

.list li {
  list-style-type: none;
}

.hidden {
  display: none;
}

.daily-forecast {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.daily-forecast > div {
    margin-bottom: 20px;
}

.city-name {
    font-size: 50px;
    color: rgb(60, 118, 224);
}

.btn {
    left: 600px;
    top: -20px;
}

.degree{
    font-size: 50px;
}

.card {
    padding: 10px;
}

